import React, { useState, useEffect, useMemo } from 'react';
import { getHistory, getReportById, deleteReport } from '../utils/history';
import { Trash2, Download, ArrowLeft } from 'lucide-react';

const getStatus = (value) => {
  if (value < 40) return { label: 'Fabricated', color: '#FF3B30' };
  if (value < 70) return { label: 'Suspicious', color: '#FFD600' };
  return { label: 'Authentic', color: '#00c853' };
};

const ScoreCircle = ({ score, label, icon, size = '130px', fontSize = '18px' }) => {
  const { label: statusLabel, color } = getStatus(score);
  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
      <div style={{
        width: size, height: size, borderRadius: '50%',
        background: color, color: 'white', fontWeight: '700',
        display: 'flex', alignItems: 'center', justifyContent: 'center',
        textAlign: 'center', fontSize: fontSize, boxShadow: '0 4px 10px rgba(0,0,0,0.15)'
      }}>
        {icon}<br />{statusLabel}<br />{score}%
      </div>
      <div style={{ marginTop: '10px', fontSize: '18px', fontWeight: '500', color: color }}>
        {label}
      </div>
    </div>
  );
};

const History = () => {
  const [historyList, setHistoryList] = useState([]);
  const [selectedReportId, setSelectedReportId] = useState(null);

  useEffect(() => { loadHistory(); }, []);

  const loadHistory = async () => {
    const data = await getHistory();
    setHistoryList(data);
  };

  const selectedReport = useMemo(() => {
    if (!selectedReportId) return null;
    return getReportById(selectedReportId);
  }, [selectedReportId]);

  const handleDelete = async (id, e) => {
    e.stopPropagation();
    if (!window.confirm('Delete this report?')) return;
    await deleteReport(id);
    loadHistory();
  };

  const handleDownloadPDF = async (report, e) => {
    e.stopPropagation();
    const { generatePDF } = await import('../utils/pdfGenerator');
    generatePDF(report);
  };

  const renderReportDetail = (report) => {
    const { fusionScore, videoScore, audioScore, textScore, fileName, timestamp, explanation } = report;
    const { color: fusionColor } = getStatus(fusionScore);
    
    return (
      <div className="analysis-result-card" style={{ padding: '40px' }}>
        <button className="glass-button secondary" onClick={() => setSelectedReportId(null)} style={{ marginBottom: '30px' }}>
          <ArrowLeft size={20} /> Back to History
        </button>

        <h1 style={{ textAlign: 'center', marginBottom: '10px', color: fusionColor }}>{fileName}</h1>
        <p style={{ textAlign: 'center', color: 'var(--text-color)', marginBottom: '30px' }}>
          Analyzed on: {new Date(timestamp).toLocaleString()}
        </p>
        <hr style={{ borderColor: fusionColor, marginBottom: '50px' }} />

        <div style={{ width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'center', marginBottom: '50px' }}>
          <ScoreCircle score={fusionScore} label="Fusion Score" icon="⭐" size="220px" fontSize="32px" />
        </div>

        <h2 style={{ textAlign: 'center', fontSize: '1.5rem', marginBottom: '30px' }}>Component Analysis</h2>
        <div style={{ width: '100%', display: 'flex', justifyContent: 'center', gap: '40px', marginBottom: '50px', flexWrap: 'wrap' }}>
          <ScoreCircle score={videoScore} label="Video" icon="🎥" size="110px" fontSize="16px" />
          <ScoreCircle score={audioScore} label="Audio" icon="🔊" size="110px" fontSize="16px" />
          <ScoreCircle score={textScore} label="Text" icon="📝" size="110px" fontSize="16px" />
        </div>
        
        <h2 style={{ fontSize: '1.5rem', marginTop: '40px', marginBottom: '20px' }}>Diagnostic Summary</h2>
        <div style={{ padding: '20px', background: 'var(--background-light)', borderRadius: '10px', border: '1px solid #e9ecef', marginBottom: '30px' }}>
          <p style={{ color: 'var(--text-color)', lineHeight: '1.6' }}>🤖: {explanation}</p>
        </div>

        <div style={{ textAlign: 'center', marginBottom: '20px', display: 'flex', gap: '10px', justifyContent: 'center' }}>
          <button className="glass-button primary" onClick={() => handleDownloadPDF(report)} style={{ padding: '15px 30px', fontSize: '16px' }}>
            <Download size={20} /> Download Report
          </button>
          <button className="glass-button danger" onClick={(e) => handleDelete(report.id, e)} style={{ padding: '15px 30px', fontSize: '16px' }}>
            <Trash2 size={20} /> Delete Report
          </button>
        </div>
      </div>
    );
  };

  const renderHistoryList = () => {
    if (historyList.length === 0) {
      return (
        <div style={{ textAlign: 'center', padding: '50px', background: 'var(--card-background)', borderRadius: '10px' }}>
          <h2 style={{ color: 'var(--primary-color)' }}>No Analysis History Found</h2>
          <p>Upload a file in the Detector page to save your first report!</p>
        </div>
      );
    }

    return (
      <div>
        {historyList.map(report => {
          const { label: statusLabel, color } = getStatus(report.fusionScore);
          return (
            <div 
              key={report.id} 
              style={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                alignItems: 'center', 
                padding: '15px 25px', 
                margin: '15px 0', 
                borderRadius: '12px',
                background: 'var(--card-background)',
                borderLeft: `5px solid ${color}`,
                boxShadow: '0 2px 5px rgba(0,0,0,0.05)',
                transition: 'transform 0.2s',
                cursor: 'pointer'
              }}
              onClick={() => setSelectedReportId(report.id)}
            >
              <div style={{ flexGrow: 1 }}>
                <p style={{ fontWeight: '600', margin: '0', fontSize: '1.1rem', color: color }}>
                  {report.fileName}
                </p>
                <small style={{ color: 'var(--text-color)' }}>
                  {new Date(report.timestamp).toLocaleString()} | Score: {report.fusionScore}% | Status: {statusLabel}
                </small>
              </div>
              <div style={{ display: 'flex', gap: '10px' }}>
                <button className="glass-button primary" onClick={(e) => handleDownloadPDF(report, e)} style={{ padding: '8px 16px', fontSize: '14px' }}>
                  <Download size={16} />
                </button>
                <button className="glass-button danger" onClick={(e) => handleDelete(report.id, e)} style={{ padding: '8px 16px', fontSize: '14px' }}>
                  <Trash2 size={16} />
                </button>
              </div>
            </div>
          );
        })}
      </div>
    );
  };

  return (
    <div style={{ maxWidth: '1200px', margin: '0 auto', padding: '20px' }}>
      <h1 style={{ textAlign: 'center', marginBottom: '10px' }}>📊 HCIS — Analysis History</h1>
      <p style={{ textAlign: 'center', color: 'var(--text-color)', marginBottom: '40px' }}>
        {selectedReportId ? 'Detailed Diagnostic Report' : 'Review past analyses and download full reports.'}
      </p>

      <div style={{ maxWidth: '900px', margin: '0 auto' }}>
        {selectedReport ? renderReportDetail(selectedReport) : renderHistoryList()}
      </div>
    </div>
  );
};

export default History;