import React, { useState } from 'react';
import { Camera, Upload, FileVideo, FileAudio, FileText, Download, X, Sparkles } from 'lucide-react';
import { addReportToHistory } from '../utils/history';
import jsPDF from 'jspdf'; // ✅ Fixed import
import axios from '../utils/axios';

// Utility functions
const formatFileSize = (bytes) => {
  if (bytes === 0) return '0 Bytes';
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};


const getStatus = (value) => {
  if (value < 40) return { label: 'Fabricated', color: '#ef4444' };
  if (value < 70) return { label: 'Suspicious', color: '#f59e0b' };
  return { label: 'Authentic', color: '#10b981' };
};

// GlassButton Component
const GlassButton = ({ children, onClick, disabled, variant = 'primary', className = '', style = {} }) => {
  const baseStyles = {
    padding: '12px 24px',
    borderRadius: '16px',
    border: 'none',
    cursor: disabled ? 'not-allowed' : 'pointer',
    fontSize: '16px',
    fontWeight: '600',
    transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
    backdropFilter: 'blur(20px)',
    WebkitBackdropFilter: 'blur(20px)',
    opacity: disabled ? 0.5 : 1,
    display: 'flex',
    alignItems: 'center',
    gap: '8px',
    justifyContent: 'center'
  };

  const variants = {
    primary: {
      background: 'rgba(59, 130, 246, 0.8)',
      color: 'white',
      boxShadow: '0 8px 32px rgba(59, 130, 246, 0.3)',
      border: '1px solid rgba(255, 255, 255, 0.18)'
    },
    secondary: {
      background: 'rgba(255, 255, 255, 0.1)',
      color: 'inherit',
      boxShadow: '0 8px 32px rgba(0, 0, 0, 0.1)',
      border: '1px solid rgba(255, 255, 255, 0.18)'
    },
    danger: {
      background: 'rgba(239, 68, 68, 0.8)',
      color: 'white',
      boxShadow: '0 8px 32px rgba(239, 68, 68, 0.3)',
      border: '1px solid rgba(255, 255, 255, 0.18)'
    }
  };

  return (
    <button
      onClick={onClick}
      disabled={disabled}
      className={`glass-button ${variant} ${className}`}
      style={{ ...baseStyles, ...variants[variant], ...style }}
      onMouseEnter={(e) => {
        if (!disabled) {
          e.currentTarget.style.transform = 'translateY(-2px) scale(1.02)';
        }
      }}
      onMouseLeave={(e) => {
        e.currentTarget.style.transform = 'translateY(0) scale(1)';
      }}
    >
      {children}
    </button>
  );
};

// ScoreCircle Component
const ScoreCircle = ({ score, label, icon: Icon, size = 'md' }) => {
  const statusInfo = getStatus(score);
  const sizes = {
    sm: { width: '120px', fontSize: '14px', iconSize: 24 },
    md: { width: '160px', fontSize: '18px', iconSize: 32 },
    lg: { width: '240px', fontSize: '28px', iconSize: 48 }
  };
  const currentSize = sizes[size];

  return (
    <div className="score-circle">
      <div 
        className="score-circle-inner"
        style={{
          width: currentSize.width,
          height: currentSize.width,
          fontSize: currentSize.fontSize,
          background: 'linear-gradient(135deg, ' + statusInfo.color + ' 0%, ' + statusInfo.color + 'dd 100%)',
          boxShadow: '0 20px 60px ' + statusInfo.color + '40'
        }}
      >
        <Icon size={currentSize.iconSize} style={{ position: 'relative', zIndex: 1 }} />
        <div style={{ position: 'relative', zIndex: 1, marginTop: '8px' }}>{score}%</div>
      </div>
      <div style={{ fontSize: '16px', fontWeight: '600', color: statusInfo.color }}>
        {label} - {statusInfo.label}
      </div>
    </div>
  );
};

// Main Detector Component
const Detector = () => {
  const [uploadedFile, setUploadedFile] = useState(null);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [analysisResult, setAnalysisResult] = useState(null);
  const [dragActive, setDragActive] = useState(false);
  const [progress, setProgress] = useState(0);

  // Drag & Drop Handlers
  const handleDrag = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === "dragenter" || e.type === "dragover") {
      setDragActive(true);
    } else if (e.type === "dragleave") {
      setDragActive(false);
    }
  };

  const handleDrop = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      setUploadedFile(e.dataTransfer.files[0]);
      setAnalysisResult(null);
    }
  };

  const handleFileInput = (e) => {
    if (e.target.files && e.target.files[0]) {
      setUploadedFile(e.target.files[0]);
      setAnalysisResult(null);
    }
  };

  // ✅ Fixed PDF Download
  const downloadPDF = () => {
    if (!analysisResult) return;

    const doc = new jsPDF();
    const statusInfo = getStatus(analysisResult.fusionScore);

    // Title
    doc.setFontSize(20);
    doc.text('HCIS Analysis Report', 20, 30);

    // File info
    doc.setFontSize(12);
    doc.text(`File: ${analysisResult.fileName}`, 20, 50);
    doc.text(`Size: ${formatFileSize(analysisResult.fileSize)}`, 20, 60);
    doc.text(`Date: ${new Date(analysisResult.timestamp).toLocaleString()}`, 20, 70);

    // Results
    doc.setFontSize(16);
    doc.text('Analysis Results:', 20, 90);

    doc.setFontSize(12);
    doc.text(`Fusion Score: ${analysisResult.fusionScore}% (${statusInfo.label})`, 20, 105);
    doc.text(`Video Score: ${analysisResult.videoScore}%`, 20, 115);
    doc.text(`Audio Score: ${analysisResult.audioScore}%`, 20, 125);
    doc.text(`Text Score: ${analysisResult.textScore}%`, 20, 135);

    // Explanation
    doc.setFontSize(16);
    doc.text('Explanation:', 20, 155);
    doc.setFontSize(12);
    const splitExplanation = doc.splitTextToSize(analysisResult.explanation, 170);
    doc.text(splitExplanation, 20, 165);

    // Footer
    doc.setFontSize(10);
    doc.setTextColor(150);
    doc.text('Generated by HCIS - Human-Computer Interaction System', 105, 280, { align: 'center' });

    // Save
    doc.save(`HCIS_Report_${analysisResult.fileName.replace(/[^a-zA-Z0-9]/g, '_')}_${Date.now()}.pdf`);
  };

  const handleAnalyze = async () => {
  if (!uploadedFile) return;
  setIsAnalyzing(true);
  setProgress(0);

  const form = new FormData();
  form.append('video', uploadedFile);
  form.append('transcript', ''); // optional

  try {
    const { data } = await axios.post('/analyze/complete', form, {
      onUploadProgress: (p) => setProgress(Math.round((p.loaded * 100) / p.total)),
    });

    const result = {
      fusionScore: data.fusionScore,
      videoScore:  data.videoScore,
      audioScore:  data.audioScore,
      textScore:   data.textScore,
      explanation: data.explanation,
      fileName:    uploadedFile.name,
      fileSize:    uploadedFile.size,
      timestamp:   new Date().toISOString(),
    };
    setAnalysisResult(result);
    addReportToHistory(result);
  } catch (err) {
    console.error(err);
    alert('Analysis failed');
  } finally {
    setIsAnalyzing(false);
  }
};


  const getProgressMessage = () => {
    if (progress < 30) return "Extracting and preprocessing data...";
    if (progress < 60) return "Running deepfake detection...";
    if (progress < 90) return "Aggregating multimodal results...";
    return "Generating summary report...";
  };

  // Render
  return (
    <div style={{ maxWidth: '1200px', margin: '0 auto', padding: '20px' }}>
      <div style={{ textAlign: 'center', marginBottom: '48px' }}>
        <h1 style={{ fontSize: '48px', fontWeight: '800', marginBottom: '16px', background: 'linear-gradient(135deg, #3b82f6 0%, #8b5cf6 100%)', WebkitBackgroundClip: 'text', WebkitTextFillColor: 'transparent' }}>
          Multimedia Content Integrity Detector
        </h1>
        <p style={{ fontSize: '18px', opacity: 0.7 }}>
          Upload a Video, Audio, or Text file to analyze for deepfake and AI-generated content
        </p>
      </div>

      {!uploadedFile && (
        <div
          onDragEnter={handleDrag}
          onDragLeave={handleDrag}
          onDragOver={handleDrag}
          onDrop={handleDrop}
          className={`dropzone-area ${dragActive ? 'drag-active' : ''}`}
          onClick={() => document.getElementById('fileInput').click()}
        >
          <Upload size={80} style={{ opacity: 0.5, marginBottom: '24px' }} />
          <p style={{ fontSize: '28px', fontWeight: '600', marginBottom: '8px' }}>
            {dragActive ? 'Drop your file here' : 'Drag & Drop Your File'}
          </p>
          <p style={{ opacity: 0.6, fontSize: '16px' }}>
            or click to select a file (MP4, MP3, TXT, etc.)
          </p>
          <input
            id="fileInput"
            type="file"
            style={{ display: 'none' }}
            onChange={handleFileInput}
            accept="video/*,audio/*,text/plain"
          />
        </div>
      )}

      {uploadedFile && !analysisResult && (
        <div className="analysis-result-card">
          <h3 style={{ marginBottom: '24px', fontSize: '20px' }}>File Ready for Analysis</h3>
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', flexWrap: 'wrap', gap: '20px' }}>
            <div>
              <p style={{ fontSize: '18px', fontWeight: '600', marginBottom: '8px' }}>
                📄 {uploadedFile.name}
              </p>
              <p style={{ opacity: 0.6, fontSize: '14px' }}>
                Size: {formatFileSize(uploadedFile.size)}
              </p>
            </div>
            <div style={{ display: 'flex', gap: '12px', flexWrap: 'wrap' }}>
              <GlassButton onClick={() => setUploadedFile(null)} variant="secondary">
                <X size={20} /> Remove
              </GlassButton>
              <GlassButton onClick={handleAnalyze} disabled={isAnalyzing}>
                <Camera size={20} /> Analyze File
              </GlassButton>
            </div>
          </div>
        </div>
      )}

      {isAnalyzing && (
        <div className="analysis-result-card" style={{ textAlign: 'center' }}>
          <div style={{ fontSize: '48px', marginBottom: '24px' }}>⚡</div>
          <h3 style={{ marginBottom: '16px', fontSize: '24px' }}>Analyzing Content...</h3>
          <p style={{ opacity: 0.7, marginBottom: '32px' }}>
            {getProgressMessage()}
          </p>
          <div style={{
            width: '100%',
            height: '8px',
            background: 'rgba(255, 255, 255, 0.1)',
            borderRadius: '4px',
            overflow: 'hidden'
          }}>
            <div style={{
              width: progress + '%',
              height: '100%',
              background: 'linear-gradient(90deg, #3b82f6, #8b5cf6)',
              transition: 'width 0.3s ease',
              borderRadius: '4px'
            }} />
          </div>
          <p style={{ marginTop: '16px', opacity: 0.6 }}>{progress}%</p>
        </div>
      )}

      {analysisResult && !isAnalyzing && (
        <div className="analysis-result-card">
          <h2 style={{ marginBottom: '40px', textAlign: 'center', fontSize: '32px' }}>
            Analysis Complete
          </h2>

          <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '60px' }}>
            <ScoreCircle score={analysisResult.fusionScore} label="Fusion Score" icon={Sparkles} size="lg" />
          </div>

          {(analysisResult.videoScore > 0 || analysisResult.audioScore > 0 || analysisResult.textScore > 0) && (
            <>
              <h3 style={{ marginBottom: '32px', textAlign: 'center', fontSize: '24px', opacity: 0.8 }}>
                Component Analysis
              </h3>
              <div style={{ display: 'flex', justifyContent: 'center', gap: '48px', marginBottom: '40px', flexWrap: 'wrap' }}>
                {analysisResult.videoScore > 0 && (
                  <ScoreCircle score={analysisResult.videoScore} label="Video" icon={FileVideo} size="md" />
                )}
                {analysisResult.audioScore > 0 && (
                  <ScoreCircle score={analysisResult.audioScore} label="Audio" icon={FileAudio} size="md" />
                )}
                {analysisResult.textScore > 0 && (
                  <ScoreCircle score={analysisResult.textScore} label="Text" icon={FileText} size="md" />
                )}
              </div>
            </>
          )}

          <div style={{
            background: 'rgba(255, 255, 255, 0.05)',
            padding: '24px',
            borderRadius: '16px',
            marginBottom: '32px'
          }}>
            <h4 style={{ marginBottom: '12px', fontSize: '18px' }}>AI Analysis Explanation</h4>
            <p style={{ opacity: 0.8, lineHeight: '1.6' }}>{analysisResult.explanation}</p>
          </div>

          <div style={{ display: 'flex', gap: '12px', justifyContent: 'center', flexWrap: 'wrap' }}>
            <GlassButton onClick={downloadPDF}>
              <Download size={20} /> Download Report
            </GlassButton>
            <GlassButton onClick={() => { setUploadedFile(null); setAnalysisResult(null); }} variant="secondary">
              Analyze Another File
            </GlassButton>
          </div>
        </div>
      )}
    </div>
  );
};

export default Detector;