import React, { useState } from 'react';
import { MessageSquare, Send, Trash2 } from 'lucide-react';
import axios from '../utils/axios';

const Chatbot = () => {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');

  const generateResponse = async (userText) => {
  const body = {
    results: messages.filter(m => m.role === 'assistant').slice(-1)[0]?.content || {},
    question: userText,
  };
  try {
    const { data } = await axios.post('/chat/explain', body);
    return data.explanation;
  } catch {
    return 'Sorry, I could not process that.';
  }
};

  const handleSend = () => {
    if (!input.trim()) return;
    
    setMessages(prev => [...prev, { role: 'user', content: input }]);
    const userInput = input;
    setInput('');

    setTimeout(() => {
      const response = generateResponse(userInput);
      setMessages(prev => [...prev, { role: 'assistant', content: response }]);
    }, 800);
  };

  return (
    <div style={{ maxWidth: '900px', margin: '0 auto', padding: '20px', height: 'calc(100vh - 100px)', display: 'flex', flexDirection: 'column' }}>
      <div style={{ textAlign: 'center', marginBottom: '32px' }}>
        <h1 style={{ fontSize: '42px', fontWeight: '800', marginBottom: '12px' }}>
          🤖 AI Assistant
        </h1>
        <p style={{ fontSize: '16px', opacity: 0.7 }}>
          Ask anything about deepfake detection and AI-generated content
        </p>
      </div>

      <div style={{
        flex: 1,
        background: 'rgba(255, 255, 255, 0.05)',
        backdropFilter: 'blur(20px)',
        borderRadius: '24px',
        padding: '24px',
        overflowY: 'auto',
        marginBottom: '20px',
        border: '1px solid rgba(255, 255, 255, 0.1)'
      }}>
        {messages.length === 0 ? (
          <div style={{ textAlign: 'center', opacity: 0.5, marginTop: '60px' }}>
            <MessageSquare size={64} style={{ marginBottom: '16px', opacity: 0.3 }} />
            <p>Start a conversation by asking a question</p>
          </div>
        ) : (
          messages.map((msg, idx) => (
            <div key={idx} style={{
              marginBottom: '16px',
              display: 'flex',
              justifyContent: msg.role === 'user' ? 'flex-end' : 'flex-start'
            }}>
              <div style={{
                maxWidth: '70%',
                padding: '16px 20px',
                borderRadius: '16px',
                background: msg.role === 'user' 
                  ? 'linear-gradient(135deg, #3b82f6, #8b5cf6)'
                  : 'rgba(255, 255, 255, 0.1)',
                backdropFilter: 'blur(10px)',
                border: '1px solid rgba(255, 255, 255, 0.1)'
              }}>
                <p style={{ margin: 0, lineHeight: '1.6' }}>{msg.content}</p>
              </div>
            </div>
          ))
        )}
      </div>

      <div style={{ display: 'flex', gap: '12px' }}>
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyPress={(e) => e.key === 'Enter' && handleSend()}
          placeholder="Type your question..."
          style={{
            flex: 1,
            padding: '16px 20px',
            borderRadius: '16px',
            border: '1px solid rgba(255, 255, 255, 0.1)',
            background: 'rgba(255, 255, 255, 0.05)',
            backdropFilter: 'blur(20px)',
            fontSize: '16px',
            outline: 'none',
            color: 'inherit'
          }}
        />
        <button 
          onClick={handleSend}
          style={{
            padding: '16px',
            borderRadius: '16px',
            border: 'none',
            background: 'linear-gradient(135deg, #3b82f6, #8b5cf6)',
            color: 'white',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            gap: '8px'
          }}
        >
          <Send size={20} />
        </button>
        {messages.length > 0 && (
          <button 
            onClick={() => setMessages([])}
            style={{
              padding: '16px',
              borderRadius: '16px',
              border: 'none',
              background: 'rgba(239, 68, 68, 0.8)',
              color: 'white',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
              gap: '8px'
            }}
          >
            <Trash2 size={20} />
          </button>
        )}
      </div>
    </div>
  );
};

export default Chatbot;