import React, { useState } from 'react';
import { Sparkles, Download } from 'lucide-react';
import axios from '../utils/axios';

const Generator = () => {
  const [file, setFile]          = useState(null);
  const [type, setType]          = useState('face-swap');
  const [text, setText]          = useState('');
  const [isGenerating, setIsGen] = useState(false);
  const [generatedUrl, setGenUrl]= useState('');

  const handleGenerate = async () => {
    if (!file) return;
    setIsGen(true);
    setGenUrl('');

    const form = new FormData();
    form.append('video', file);
    form.append('text', text);

    try {
      const { data } = await axios.post('/generate/video-with-audio', form);
      setGenUrl(data.downloadUrl);
    } catch (err) {
      alert('Generation failed');
    } finally {
      setIsGen(false);
    }
  };

  return (
    <div className="gen-page">
      <div className="gen-header">
        <h1>🎭 Content Generator</h1>
        <p>Generate synthetic media for educational and research purposes</p>
      </div>

      <div className="gen-card glass">
        <div className="gen-field">
          <label>Generation Type</label>
          <select value={type} onChange={(e) => setType(e.target.value)} className="gen-select">
            <option value="face-swap">Face Swap</option>
            <option value="voice-clone">Voice Clone</option>
            <option value="text-generation">Text Generation</option>
          </select>
        </div>

        <div className="gen-field">
          <label>Upload Source File</label>
          <input type="file" onChange={(e) => setFile(e.target.files[0])} className="gen-file" />
          {file && <p className="file-name">Selected: {file.name}</p>}
        </div>

        {type === 'voice-clone' && (
          <div className="gen-field">
            <label>Target Text</label>
            <textarea value={text} onChange={(e) => setText(e.target.value)} className="gen-textarea" />
          </div>
        )}

        <button onClick={handleGenerate} disabled={!file || isGenerating} className="gen-btn glass-button primary">
          <Sparkles size={20} />
          {isGenerating ? 'Generating…' : 'Generate Content'}
        </button>

        {generatedUrl && (
          <a href={generatedUrl} download={`generated_${file.name}`} className="download-btn glass-button secondary">
            <Download size={20} /> Download Result
          </a>
        )}
      </div>
    </div>
  );
};

export default Generator;